@import "@folio/stripes-components/lib/variables.css";

.fullWidthAndHeightContainer {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;

  &:empty {
    display: none;
  }
}

.cellAppIcon {
  align-items: baseline;
  flex-shrink: initial;
}

/* disable styles for visited links created by react-router-dom Link */
.linkButton,
.linkButton:visited,
.linkButton:hover,
.linkButton:active {
  color: inherit;
}

.endOfRecord {
  margin-top: -1rem;
  text-align: left;
}

.checkbox {
  &:disabled {
    margin: 0;
  }
}

.cellLink {
  margin-top: 0.25rem !important;
  margin-bottom: 0.25rem !important;
  color: inherit !important;
}

.modifyActionIcon {
  width: 14px;
  min-width: 14px;
  height: 14px;
}

.preloader {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1rem 0;
}

.separatorLine {
  border-bottom: 5px solid var(--primary);
}

.headline {
  word-break: break-word;
}

.headlineMargins {
  margin: 2.5rem 1rem;
}

.leftMargin {
  margin-left: 5px;
}

.sideMargins {
  margin: 0 20px;
}

hr {
  margin: 0;
}

.baselineCell {
  display: inline-block;
  align-self: baseline;
}
