.richTextButton:hover {
    color: #06c;
}

.loadingContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh; /* or 100% based on your requirement */
    width: 100%;
}

.loadingSpinner {
    border: 8px solid #f3f3f3; /* Light grey */
    border-top: 8px solid #3498db; /* Blue */
    border-radius: 50%;
    width: 30px;
    height: 30px;
    animation: spin 2s linear infinite;
    margin-bottom: 16px;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

.statusError {
    color: var(--error);
}

.statusWarn {
    color: var(--warn);
}

.statusSuccess {
    color: var(--success);
}
