@import "@folio/stripes-components/lib/sharedStyles/form.css";

.coverageDatesList {
  &:nth-child(odd) {
    background-color: transparent;
  }

  & li {
    margin-bottom: 0;
  }
}

.limit-error {
  position: relative;
  top: -0.35rem;
  margin-left: 1.25rem;
  padding: 0 var(--input-vertical-padding);
  font-size: var(--font-size-medium);
  color: var(--error);
}
