.previewWrapper {
  position: absolute;
  right: 40px; 
  top: 20px;
  padding: 10px;
  background-color: #f9f9f9;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 8px; 
  width: 30%;
}

.headlineStyle {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-content: center;
  border-bottom-width: 20px;
  border-bottom: #afafaf;
}

.cardWrapper {
  display: flex;
  flex-flow: row wrap;
}

.cardStyle {
  width: 47%;
  height: 47%;
  background-color: #e7e3d8;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 8px; 
  margin: 1.5%;
}

.rowStyle {
  display: flex;
  justify-content: flex-start;
}

.colStyle {
  margin-right: 5%;
}